.footer {
    margin: 0 auto;
    padding-top: 66px;
    padding-bottom: 60px;
    max-width: 880px;
}

@media screen and (max-width: 879px) {
    .footer {
        max-width: 581px;
    }
}

@media screen and (max-width: 580px) {
    .footer {
        padding-top: 48px;
        padding-bottom: 36px;
        max-width: 282px;
    }
}